.menu-open {
  position: fixed;
  top: 25px;
  left: 30px;
  width: 40px;
  height: 30px;
  padding-top: 15px;
  z-index: 9999;
}

@media only screen and (max-width: 767px) {
  .menu-open {
    left: 20px;
  }
}

.menu-open:hover span {
  background: #78985c;
}

.menu-open:hover span:before {
  background: #78985c;
}

.menu-open:hover span:after {
  background: #78985c;
}

.menu-open span {
  width: 30px;
  height: 2px;
  background: #000000;
  display: block;
  transition: color 0.3s;
  transition: width 0.5s ease;
  border-radius: 20%;
}

.menu-open span:before {
  content: '';
  position: absolute;
  left: 0;
  width: 25px;
  height: 2px;
  background: #000000;
  transition: color 0.3s;
  transition: transform 0.5s;
  top: 6px;
  border-radius: 20%;
}

.menu-open span:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 4px;
  width: 15px;
  height: 2px;
  background: #000000;
  transition: color 0.3s;
  transition: transform 0.5s;
  border-radius: 20%;
}

.menu-open.active span {
  width: 0;
}

.menu-open.active span:before {
  width: 30px;
  transform: rotate(225deg);
  top: 14px;
}

.menu-open.active span:after {
  transform: rotate(315deg);
  width: 30px;
  bottom: 14px;
}

