.form__checkbox input + label::before {
  content: '';
  font-family: 'FontAwesome';
  width: 16px;
  height: 16px;
  border: 1px solid #a0b77b;
  border-radius: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  text-align: center;
  right: 10px;
  color: #a0b77b;
}

@media only screen and (max-width: 1024px) {
  .form__checkbox input + label::before {
    position: absolute;
    left: -5px;
